<template>
  <div class="trade">
    <div>
      <div class="invited">
        <h1>{{ $t('translateLang.tradeDetail.text51') }}</h1>
        <h5>{{ $t('translateLang.tradeDetail.text52') }}</h5>
        <router-link to="/invitation" tag="div" class="invited-btn">{{ $t('translateLang.tradeDetail.text53') }}</router-link>
      </div>
      <div class="trade-content">
        <div class="clearfix search-box">
          <div class="search-label">{{ $t('translateLang.tradeDetail.text54') }}</div>
          <div class="search">
            <el-input v-model="searchContent" placeholder=""><i slot="suffix" class="el-input__icon el-icon-search"></i></el-input>
        </div>
        </div>
        <div class="flow-container">
          <vue-scroll :ops="ops">
            <div class="trade-area-tap trade-coin-tap">
              <div v-for="(item, index) in tradeList" :key="item.trade_area_cate_id">
                <p class="currency" :class="{active:areaActive === index}" @click="handleAreaClick(item.trade_area_cate_id, item.trade_area_list, index)">
                  {{ item.area_cate_name }}
                </p>
              </div> 
            </div>
          </vue-scroll>
        </div>
        

        <div>
          <div class="trade-coin-tap trade-area-coin-tap">
            <div v-for="(item, index) in tradeAreaList" :key="item.trade_area_id">
              <p class="currency" :class="{active:coinActive === index}" @click="handleClick(item.trade_area_id,  index)">
                {{ item.currency_mark }}
              </p>
              <!-- <p v-else-if="index === tradeAreaList.length - 1" class="currency"  @click="handleClick(item.trade_area_id,  $event)">
                {{ item.currency_mark }}
              </p> -->
            </div>
          </div>
        </div>
     </div>
    

    <trade-table :id="trade_area_id" :typeId="typeId"  :searchContent="searchContent" @initTrade="initTrade"></trade-table>
    </div>
  </div>
</template>

<script>
  import tradeTable from './tradeTable'
  import { getToken } from '@/utils/auth'
  import { getAllTradeAreaCateList } from '@/api/index'
  export default {
    components: { tradeTable },
    data() {
      return {
        tradeList: [],
        activeName: '',
        trade_area_id: 0,
        trade_area_cate_id: 0,
        typeId: 0,
        searchContent: '',
        tradeAreaList: [],
        areaActive: 0,
        coinActive: 0,
        ops: {
          vuescroll: {
            mode: 'slide',
            sizeStrategy: 'percent',
            detectResize: true
          },
          scrollPanel: {
            initialScrollY: false,
            initialScrollX: true,
            scrollingX: true,
            scrollingY: false,
            speed: 100
            // easing: undefined,
            // padding: true
            // verticalNativeBarPos: 'bottom'
          },
          rail: {
            background: '#131625',
            // opacity: 0,
            size: '3px',
            specifyBorderRadius: false,
            gutterOfEnds: '0px',
            gutterOfSide: '0px',
            keepShow: true
          },
          bar: {
            showDelay: 200,
            onlyShowBarOnScroll: true,
            keepShow: false,
            background: '#363e65',
            opacity: 1,
            size: '3px',
            hoverStyle: false
          }
        }
      }
    },
    created() {
      this.initTrade()
    },
    methods: {
      initTrade() {
        const sendData = {}
        if (window.localStorage.getItem('link_userInfo') && getToken()) {
          let link_userInfo = window.localStorage.getItem('link_userInfo')
          link_userInfo = JSON.parse(link_userInfo)
          sendData.user_id = link_userInfo.uid
        }
        getAllTradeAreaCateList(sendData).then(res => {
          if (res.status !== 1000) {
            return false
          }
  
          this.tradeList = [...res.data]
          console.log(this.tradeList)
  
          this.typeId = res.data[0].trade_area_cate_id
          this.tradeAreaList = res.data[0].trade_area_list
          this.trade_area_id = res.data[0].trade_area_list[0].trade_area_id
        }).catch(() => {
        })
      },
      handleAreaClick(id, trade_area_list, index) {
        this.areaActive = index
        this.coinActive = 0
        this.tradeAreaList = trade_area_list
        if (trade_area_list.length !== 0) {
          this.trade_area_id = trade_area_list[0].trade_area_id
        }
        this.typeId = id
      },
      handleClick(id, index) {
        this.coinActive = index

        this.trade_area_id = id
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .trade {
    .invited{
      position: relative;
      width: 100%;
      height: 140px;
      margin-bottom: 8px;
      background: url('/static/images/trade_detail/invite_bg.png');
      h1,
      h5{
        color: #b0b8db;
        font-weight: normal;
        padding-left: 14px;
      }
      h1{
        font-size: 22px;
        padding-top: 40px;
      }
      h5{
        font-size: 15px;
        padding-top: 5px;
      }
      .invited-btn{
        position: absolute;
        right: 15px;
        top: 70px;
        width: 97px;
        height: 30px;
        line-height: 30px;
        text-align: center;
        font-size: 14px;
        border-radius: 2px;
        background: rgba(21, 135, 231, 1);
        color: white;
        cursor: pointer;
      }
    }
    .trade-content{
      background: #191b2b;
    }
    .flow-container{
      width: 300px;
      overflow: hidden;
      overflow-x: hidden;
    }
    .trade-area-coin-tap{
      p.currency{
        &.active{
          border-bottom: 1px solid #1fa8f6 !important;
          background: none !important;
          color: #1fa8f6 !important;
        }
      }
    }
    .trade-area-tap{
      width: 500px;
      p.currency{
        border-radius: 4px;
        &.active{
          background: #1fa8f6;
          color: white;
        }
      }
    }
    .trade-coin-tap{
      text-align: left;
      margin-top: 8px;
      color: #b0b8db;
      
      // display: flex;
      >div{
        display: inline-block;
        p.currency{
          line-height: 23px;
          margin: 0 4px;
          padding: 0 4px;
          display: inline-block;
          cursor: pointer;
          font-size: 12px;
          vertical-align: middle;
          img{
            margin-right: 5px;
          }
          
        }
      }
    }
    .trade-area-coin-tap{
      background: #131625 !important;
      line-height: 35px !important;
    }
    .border-l{
      border-left: 1px solid #e4e8ec !important;
    }
    .border-r{
      border-right: 1px solid #e4e8ec !important;
    }
    .title{
      text-align: center;
      line-height: 70px;
      font-weight: bold;
      color: #2e3544;
      font-size: 30px;
    }
    .title-tips{
      text-align: center;
      margin-bottom: 70px !important;
      font-size: 18px;
      color: #5d667a;
    }
    .search-box{
      // background: #131625;
      width: 100%;
      vertical-align: middle;
      padding: 7px 15px;
      display: -webkit-box;
      -webkit-box-sizing: border-box;
      box-sizing: border-box;
      .search-label{
        line-height: 28px;
        padding-right: 8px;
        font-size: 14px;
        line-height: 30px;
      }
    }
    .search{
      width: 210px;
      display: inline-block;
    }
    /deep/.el-input__inner{
      height: 30px;
      line-height: 30px;
      background-color:#131625;
      border: 1px solid #3a3f58;
    }
    /deep/.el-input__icon{
      line-height: 30px;
    }
    /deep/.el-table td, /deep/.el-table th.is-leaf{
      border: 0px solid #191b2b;
    }
    /deep/.el-table--border::after, /deep/.el-table--group::after,/deep/ .el-table::before{
      background-color: #191b2b;
    }
    /deep/.el-table, /deep/.el-table__expanded-cell{
      background-color: #191b2b;
    }
  }
</style>



